
.container{
    
    width: 307px;
    height: 470px;
    border-radius: 12px;
    background-image: linear-gradient(180deg,#130754 0%,#3b2f80 100%);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);

}
.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 20px;
}
.top-bar input{
   display: flex;
   width: 186px;
   height: 40px;
   background: #ebfffc;
   border: none;
   outline: none;
   border-radius: 40px;
   padding-left: 40px;
   color: #626262;
   font-size: 20px;
   font-weight: 400px;
}
.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
}
.weather-image{
    font-size: 0.9em;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    font-size: 16px;;
}
.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400;
}
.weather-location{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 30px;
    font-weight: 400;
}
.data-container{
   margin-top:10px; 
   color: white;
   display: flex;
   justify-content: center;
}
.eliment{
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap:12px;
}
.data{
    margin-top: 5px;
    font-size: 17px;
    font-weight: 400;
}
.text{
    margin-top: 5px;
    font-size: 10px;
    font-weight: 400;
}
.icon{
    margin-top: 10px;
}

/* --------------------------------------------  Media Queries ---------------------------------------- */

@media screen and (max-width: 500px) {
    .container {
      /* background-color: rgb(37, 23, 29); */
     /* background: url(./images/w6.jpg);*/
      width: 100%;
      height: 100vh;
    }
  
    .top-bar input{
      margin-top: 5%;
      width: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 20%;
    }
  
    .search-icon {
      margin-top: 5%;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      background: #ebfffc;
      border-radius: 40px;
      cursor: pointer;
    }
  
    .col {
      display: flex;
      flex-direction: column;
      align-items: center;
  
      text-align: center;
    }
    .data{
        margin-top: 115px;
        font-size: 17px;
        font-weight: 400;
    }
    .text{
        margin-top: 5px;
        font-size: 10px;
        font-weight: 400;
    }
    .icon {
      
      font-size: 28px;
      margin-top: 115px;
    }
  }